var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('v-layout',{attrs:{"row":"","wrap":"","mt-3":"","mb-3":""}},[_c('h1',{staticClass:"display-0",staticStyle:{"background-color":"#808080","color":"#ffffff"}},[_vm._v("Online Projector Deblurring Using a Convolutional Neural Network")])]),_c('strong',[_vm._v("Introduction")]),_c('p',[_vm._v("\n            Projector deblurring is an important technology for dynamic projection mapping (PM), where the distance between a projector and a projection surface changes in time. However, conventional projector deblurring techniques do not support dynamic PM because they need to project calibration patterns to estimate the amount of defocus blur each time the surface moves. We present a deep neural network that can compensate for defocus blur in dynamic PM. The primary contribution of this paper is a unique network structure that consists of an extractor and a generator. The extractor explicitly estimates a defocus blur map and a luminance attenuation map. These maps are then injected into the middle layers of the generator network that computes the compensation image. We also propose a pseudo-projection technique for synthesizing physically plausible training data, considering the geometric misregistration that potentially happens in actual PM systems. We conducted simulation and actual PM experiments and confirmed that: (1) the proposed network structure is more suitable than a simple, more general structure for projector deblurring; (2) the network trained with the proposed pseudo-projection technique can compensate projection images for defocus blur artifacts in dynamic PM; and (3) the network supports the translation speed of the surface movement within a certain range that covers normal human motions.\n            "),_c('br'),_vm._v("Our networks are available at "),_c('a',{attrs:{"href":"https://github.com/kagechan5/Online-Projector-Deblurring-Using-a-Convolu